.menu_cont{position:fixed;top:0;left:0;width:18rem;height:100%;display:flex;flex-direction:column;border-right:1px solid rgba(78,78,80,0.5);transition:all .6s ease;z-index:9}.menu_cont .avatar_cont{height:40%;display:flex;align-items:center;justify-content:center;padding:2rem 0;border-bottom:1px solid rgba(78,78,80,0.5)}.menu_cont .avatar_cont img{width:13rem;height:13rem;border-radius:50%;border:6px solid rgba(78,78,80,0.5);object-fit:cover;filter:grayscale(0.5);-webkit-filter:grayscale(0.5)}.menu_cont_links{height:60%;flex:1 1 100%}.menu_cont_links ul{height:100%;display:flex;flex-direction:column;align-items:center;justify-content:center;margin-top:-2rem}.menu_cont_links ul li{display:block;width:100%}.menu_cont_links ul a{display:block;width:100%;font-size:1.1rem;text-align:center;padding:.6rem;position:relative;transition:all .6s ease;z-index:1}.menu_cont_links ul a:before{content:"";position:absolute;top:0;left:0;height:100%;width:0;background-color:rgba(195,8,63,0.1);transition:all .6s ease;z-index:-1}.menu_cont_links ul a:hover{color:rgba(255,255,255,0.5);font-weight:500}.menu_cont_links ul a:hover::before{width:100%;background-color:rgba(195,8,63,0.2)}.menu_cont_links ul a.active{color:white;font-weight:500}.menu_cont_links ul a.active::before{width:100%;background-color:rgba(195,8,63,0.5)}.menu_cont .menu_toggle{position:absolute;top:37px;right:-63px;z-index:2;display:none}@media screen and (max-width: 900px){.menu_cont .menu_toggle{display:block}}.menu_cont .menu_toggle a{background-color:#1A1A1C;padding:.8rem 1.2rem;border:1px solid #343436;border-left-color:transparent;display:flex;align-items:center;justify-content:center;cursor:pointer}.menu_cont .menu_toggle a i{font-size:1.8rem}.menu_cont.close{left:-18rem}
