.contact_sec{margin-top:4rem;width:50%;display:flex;flex-direction:column}@media screen and (max-width: 1200px){.contact_sec{width:100%}}.contact_sec_panel{background-color:#1A1A1C;display:flex;padding:2rem;margin-bottom:2rem;align-items:flex-start}@media screen and (max-width: 600px){.contact_sec_panel{flex-direction:column}}.contact_sec_panel .icon{border:1px solid rgba(78,78,80,0.5);display:flex;align-items:center;justify-content:center;padding:1.4rem;margin-right:1rem}@media screen and (max-width: 600px){.contact_sec_panel .icon{margin-right:0;margin-bottom:1rem}}.contact_sec_panel .icon i{font-size:2rem}.contact_sec_panel .text h4{font-size:1.4rem;font-weight:600;margin-bottom:1.2rem}.contact_sec_panel .text a{color:#a4acc4;font-size:1.2rem;display:block;margin-bottom:.5rem;font-weight:300;transition:all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1)}.contact_sec_panel .text a:hover{color:#C3083F}.contact_sec.section-form .input_group{margin-top:30px;position:relative}.contact_sec.section-form .input_group label{position:absolute;left:15px;top:-13px;background:#10121b;transition:all .4s ease-out;pointer-events:none;padding:0 10px;font-size:.94rem}.contact_sec.section-form .input_group input{border:1px solid #2e344e;font-size:1rem;height:44px;padding:0 15px;background:transparent;border:1px solid #2e344e;font-family:Nunito,sans-serif;color:#a4acc4}
