.portfolio_page .port_items{display:flex;justify-content:space-between;flex-wrap:wrap;margin-top:3rem}.portfolio_page .port_items .port_item{width:31.5%;margin-bottom:3rem;display:flex;flex-direction:column}@media screen and (max-width: 750px){.portfolio_page .port_items .port_item{width:100%}}.portfolio_page .port_items .port_item .back_img{width:100%;height:170px;margin-bottom:1.5rem;background-size:cover;background-repeat:no-repeat;position:relative;display:flex;align-items:center;justify-content:center}@media screen and (max-width: 750px){.portfolio_page .port_items .port_item .back_img{height:180px;background-size:cover;margin-bottom:1rem}}.portfolio_page .port_items .port_item .back_img .link_div{position:absolute;left:15px;top:15px;height:calc(100% - 30px);width:0;background:rgba(255,255,255,0.678);transition:all .4s ease-in-out}.portfolio_page .port_items .port_item .back_img a.app_link{background-color:rgba(78,78,80,0.8);display:flex;align-items:center;justify-content:center;padding:1rem;border-radius:50%;visibility:hidden;transform:translateY(2rem);transition:all .4s ease-in-out}.portfolio_page .port_items .port_item .back_img a.app_link i{font-size:1.2rem;color:#eee}.portfolio_page .port_items .port_item .back_img a.app_link:hover{background-color:#6F112E}.portfolio_page .port_items .port_item .back_img:hover .link_div{width:calc(100% - 30px)}.portfolio_page .port_items .port_item .back_img:hover a{visibility:visible;transform:translateY(0)}.portfolio_page .port_items .port_item h3{font-size:1.4rem;color:#ccc;font-weight:700;margin-bottom:.7rem;width:100%}@media screen and (max-width: 750px){.portfolio_page .port_items .port_item h3{margin-bottom:.3rem}}.portfolio_page .port_items .port_item p{width:100%;color:#a4acc4;font-size:1rem;line-height:1.3rem}.portfolio_page .pagination{display:flex;list-style:none;cursor:pointer}.portfolio_page .pagination{margin-top:1rem}.portfolio_page .pagination a{margin-right:5px;display:inline-block;border:1px solid #6F112E;height:40px;width:40px;text-align:center;color:#B40639;line-height:38px}.portfolio_page .pagination__link{font-weight:bold}.portfolio_page .pagination__link--active a{color:#fff;background:#6F112E;border-color:#6F112E}.portfolio_page .pagination__link--disabled a{display:none}
