.homepage{display:flex;align-items:center;justify-content:center;position:relative}.homepage #tsparticles{position:absolute;width:100%;height:100%;left:0;top:0;z-index:1}.homepage_section{text-align:center;width:80%;margin:auto;position:relative;z-index:9}@media screen and (max-width: 600px){.homepage_section{width:100%}}.homepage_section h1{font-size:3.2rem;color:white;font-weight:bold;margin-bottom:1.5rem;margin-top:2rem}@media screen and (max-width: 600px){.homepage_section h1 .co_main{display:block}}.homepage_section p{font-size:1.3rem;line-height:1.6;width:90%;margin:0 auto}.homepage_section .links{display:flex;align-items:center;justify-content:center;margin-top:1.5rem}.homepage_section .links a{margin:0 .7rem;display:block;border-radius:50%;border:2px solid #4E4E50;padding:.8rem;text-transform:uppercase;transition:all 0.5s cubic-bezier(0.075, 0.82, 0.165, 1)}.homepage_section .links a i{font-size:1.1rem;color:#a4acc4;transition:all 0.8s cubic-bezier(0.075, 0.82, 0.165, 1)}.homepage_section .links a:hover{border-color:#C3083F}.homepage_section .links a:hover i{color:#C3083F}
